import React, { useState, useEffect,  Suspense } from 'react'
// get tour location from URL
const url = window.location.href as string;
let index = url.lastIndexOf('/') as number;
let urlSlice = url.slice(index + 1) as string;


const TourComponent = React.lazy(() => import(`./tour/${urlSlice}`));



export default function Tour() {
  return (
     <Suspense fallback={<div>Loading...</div>}>
        <TourComponent/>
     </Suspense>
  );
}
