import React , { useState, useEffect, ChangeEvent} from 'react'


export default function Contact() {

const [firstName, setFirstName] = useState("");
const [lastName, setLastName] = useState('');
const [phoneNumber, setPhoneNumber] = useState(0);
const [email, setEmail] = useState('');
const [message, setMessage] = useState('');

useEffect(() =>{
    let fname = document.getElementById('firstName')?.innerText || ""
    let lname = document.getElementById('lastName')?.innerText || ""
    let number = Number(document.getElementById('contactNumber')?.innerText) || 0
    let email = document.getElementById('email')?.innerText || ""
    let message = document.getElementById('yourMessage')?.innerText || ""
        setFirstName(fname);
        setLastName(lname);
        setPhoneNumber(number);
        setEmail(email);
        setMessage(message);
},[]);

function onChangefirstName(e: ChangeEvent<HTMLInputElement>)
{
    setFirstName(e.target.value);
}

function onChangelastName(e: ChangeEvent<HTMLInputElement>)
{
    setLastName(e.target.value);
}

    
function onChangeNumber(e: ChangeEvent<HTMLInputElement>)
{
    setPhoneNumber(Number(e.target.value));
}
function onChangeEmail(e: ChangeEvent<HTMLInputElement>)
{
    setEmail(e.target.value);
}
function onChangeMessage(e: ChangeEvent<HTMLTextAreaElement>)
{
    setMessage(e.target.value);
}


function formSubmit(e: any)
{
    e.preventDefault();
    console.log(`
    Name:             ${`${firstName} ${lastName}`}
    PhoneNumber:      ${phoneNumber}
    Email:            ${email}
    Message:          ${message}
    `);
}




  return (
    <div className="container">
    <section id="contact" className="row">

        <div className="offset-md-2 col-md-8 mt-5 pt5">

            <div className="panel panel-primary">
                <div className="panel-heading">
                    <h3 className="panel-title">Contact Us</h3>
                </div>
                <div className="panel-body">
                    <form id="contactForm" className="form">
                        <div className="form-group">
                            <label htmlFor="firstName">First Name</label>
                            <input type="text" className="form-control"  id="firstName"onChange={onChangefirstName} name="firstName" placeholder="First Name" required/>
                        </div>
                        <div className="form-group">
                            <label htmlFor="lastName">Last Name</label>
                            <input type="text" className="form-control"onChange={onChangelastName}id="lastName" name="lastName" placeholder="Last Name" required/>
                        </div>
                        <div className="form-group">
                            <label className="form-label" htmlFor="contactNumber">Contact Number</label>
                            <input type="tel" className="form-control" onChange={onChangeNumber} id="contactNumber" name="email" placeholder="Contact Number" required/>
                        </div>
                        <div className="form-group">
                            <label className="form-label" htmlFor="email">Email</label>
                            <input type="email" className="form-control" id="email" onChange={onChangeEmail} name="email" placeholder="Email" required/>
                        </div>
                        <div className="form-group">
                            <label htmlFor="comments">Your Message</label>
                            <textarea className="form-control" rows={5} id="yourMessage" onChange={onChangeMessage} name="comments" placeholder="Enter Your Comments Here" required ></textarea>
                        </div>
                        <div className="text-right">
                            <button id="sendButton" type="submit" className="btn btn-primary" onClick={formSubmit}>Send</button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    </section>
</div>
  )
}





