import React from 'react'
import { NavLink } from 'react-router-dom';
import Dropdown from "react-bootstrap/Dropdown";
export default function header() {
  return (
     <nav className='navbar navbar-expand-lg navbar-dark bg-dark fixed-top'>
        <div className='container-fluid'>
           <NavLink className='navbar-item' to='/'>
              <i className='fa-solid fa-earth-europe fa-lg'></i>
           </NavLink>

           <button
              className='navbar-toggler'
              type='button'
              data-bs-toggle='collapse'
              data-bs-target='#navbarSupportedContent'
              aria-controls='navbarSupportedContent'
              aria-expanded='false'
              aria-label='Toggle navigation'
           >
              <span className='navbar-toggler-icon'></span>
           </button>

           <div
              className='collapse navbar-collapse'
              id='navbarSupportedContent'
           >
              <ul className='navbar-nav ms-auto mb-2 mg-lg-0'>
   

                 {/*dropdown*/}
                <Dropdown>
                <Dropdown.Toggle variant="success" id="dropdown-basic" className="me-3"><i className='fa-solid fa-earth-europe fa-lg'></i> Tour</Dropdown.Toggle>
                <Dropdown.Menu>
                    <Dropdown.Item href="/tour/alonissos">Alonissos</Dropdown.Item>
                    <Dropdown.Item href="/tour/amorgos">Amorgos</Dropdown.Item>
                    <Dropdown.Item href="/tour/folegandros">Folegandros</Dropdown.Item>
                    <Dropdown.Item href="/tour/gavdos">Gavdos</Dropdown.Item>
                    <Dropdown.Item href="/tour/hydra">Hydra</Dropdown.Item>
                    <Dropdown.Item href="/tour/ithaca">Ithaca</Dropdown.Item>
                    <Dropdown.Item href="/tour/milos">Milos</Dropdown.Item>
                    <Dropdown.Item href="/tour/spetses">Spetses</Dropdown.Item>
                    <Dropdown.Item href="/tour/syros">Syros</Dropdown.Item>
                </Dropdown.Menu>
                </Dropdown>

                 <li>
                       <NavLink className='navbar-item nav-link mt-auto mb-auto me-3' to='/contact'>
                      <i className='fa fa-envelope fa-lg'></i> Contact 
                    </NavLink>
                 </li>
<li>
                       <NavLink className='navbar-item nav-link mt-auto mb-auto me-3' to='/about'>
                      About Author
                    </NavLink>
                 </li>

              </ul>
           </div>
        </div>
     </nav>
  );
}
